.search-bar {
  position: sticky;
  top: 0;
  height: 3.5rem;
  background-color: #2a2c2e;
  padding: .5rem 1.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 2;

  &-input {
    color: white;
    padding: .5rem;
    background-color: #9ca3af;
    border-radius: .75rem;
    width: 100%;
    margin-left: 1rem;

    @include media(sm) {
      width: 285px;
    }

    &:focus {
      --var-width: 2px;
      box-shadow: inset 0 0 0 var(--var-width) #fcd34d,inset 0 0 0 calc(var(--var-width)) #fcd34d, 0 0 transparent;
      outline: var(--var-width) solid transparent;
      outline-offset: var(--var-width);
    }

    &::placeholder {
      color: #dddddd;
      font-style: italic;
    }
  }

  .search-bar-input-container {
    display: flex;
    align-items: center;

    .input-icon {
      @extend .cursor-pointer;
      width: 24px;
      height: 24px;
      position: absolute;
      right: 2rem;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #9ca3af;

      svg {
        width: 20px;
        height: 20px;
      }
    }
  }
}
